/* Main Component */
.container {
  background: #373632;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.game-info {
    margin-top: 5%;
    width: 45%;
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.game-info-text {
    font-family: monospace;
    text-align: center;
    color: #a6a6a6;
    font-size: 1.4em;
    line-height: 1.5em;
  }
  

.logo {
  width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  /* margin-top: 80px; */
}

.power-btn {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    text-decoration: none;
    font-size: inherit;
    font-family: inherit;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
  
    font-weight: 600;
    color: #382b22;
    text-transform: uppercase;
    padding: 1.25em 2em;
    background: #fff0f0;
    border: 2px solid #b18597;
    border-radius: 0.75em;
    transform-style: preserve-3d;
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), background 150ms cubic-bezier(0, 0, 0.58, 1);
  }
  
  .power-btn::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f9c4d2;
    border-radius: inherit;
    box-shadow: 0 0 0 2px #b18597, 0 0.625em 0 0 #373632;
    transform: translate3d(0, 0.75em, -1em);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
  }
  
  .power-btn:hover {
    background: #ffe9e9;
    transform: translate(0, 0.25em);
  }
  
  .power-btn:hover::before {
    box-shadow: 0 0 0 2px #b18597, 0 0.5em 0 0 #373632;
    transform: translate3d(0, 0.5em, -1em);
  }
  
  .power-btn:active {
    background: #ffe9e9;
    transform: translate(0em, 0.75em);
  }
  
  .power-btn:active::before {
    box-shadow: 0 0 0 2px #b18597, 0 0 #373632;
    transform: translate3d(0, 0, -1em);
  }


/* Bootup Component */
.video-player {
  background: black;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}


/* Desktop Component */
.wallpaper {
  height: 100vh;
  background: #416A9A;
  background-image: url('https://img.wallpapersafari.com/desktop/1920/1080/66/29/6I9SEj.jpg');
  background-size: cover;
  background-position: 50% 1%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}








